// src/store/index.js
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
//vuex
/*
1.2
store
mapState
mapState Vuex  Vuex  Vue 

2.mutations
vuex
2.1mutations
1.1.
    2.mutationsstate
    3.mutations
    this.$store.commit('add'obj)

*/
export default new Vuex.Store({
  state: {
    // data  
    user: JSON.parse(localStorage.getItem('user')) || null,
    jwt: localStorage.getItem('jwt') || '',
    taskList: []
  },
  mutations: {
    setUser(state, user) {
      state.user = user
      //user
      localStorage.setItem('user', JSON.stringify(user)); //  localStorage
      localStorage.setItem('jwt', user.token)
    },
    clearUser(state) {
      state.user = null;
      localStorage.removeItem('user'); //  localStorage
      localStorage.removeItem('jwt')
    },
    addTask(state, taskId) {
      state.taskList.push(taskId)
    },
    clearTask(state) {
      //
      state.taskList = []
    }
  },
  actions: {
  },
  getters: {
    vipIsAvailable(state) {
      let user = state.user
      if (user == null) {
        return false
      }
      if (new Date().getTime() - user.expiration < 0) {
        return true
      }
      return false
    }
  }
});


