<template>
    <div class="map-list-container widget">
        <el-dropdown>
            <el-button>{{ nowBaseLayerName }}<i class="el-icon-arrow-down el-icon--right"></i></el-button>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(source, index) in baseLayerConfig.baseLayerList" :key="index"
                    :class="{ 'bord': index == baseLayerConfig.chosenSourceId }" @click.native="changeBaseMap(index)">
                    {{ source.name }}
                </el-dropdown-item>
                <el-dropdown-item @click.native="dialogVisible = !dialogVisible" divided></el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>


        <!--  -->
        <el-dialog :visible.sync="dialogVisible" title="" :append-to-body="true" width="50%">
            <!--  -->
            <el-table :data="baseLayerConfig.baseLayerList" style="width: 100%">
                <el-table-column prop="name" label="">
                </el-table-column>
                <el-table-column label="" width="120px">
                    <template slot-scope="scope">
                        <span v-if="scope.row.owner == -1" style="color: green;"></span>
                        <el-button v-else size="mini" type="danger" @click="del(scope.$index)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <div style="margin-top: 20px;text-align: right">
                <el-button type="success" @click="addDialogVisible = true"></el-button>
            </div>


            <!--  -->
            <el-dialog :visible.sync="addDialogVisible" title="" width="50%" :append-to-body="true">
                <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="URL" prop="url">
                        <el-input v-model="form.url"></el-input>
                    </el-form-item>
                    <el-form-item>
                        urlhttp://www.map.org/{z}/{x}/{y}.png,{z}{x}{y}
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="saveMapSource" :loading="saveIsLoading"> </el-button>
                </span>
            </el-dialog>

        </el-dialog>
    </div>

</template>
<script>
import { baseLayerConfig } from '@/util/mapManager.js'
import checkUtil from '@/util/commonUtil.js'
import { addMapSource, delMapSource } from '@/util/request.js'

export default {
    name: 'MapList',
    methods: {
        del(index) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                // 
                let baseLayer = baseLayerConfig.baseLayerList[index]
                let id = baseLayer.id
                delMapSource(id).then(
                    res => {
                        if (res.resultCode == 1) {
                            let sourceArr = res.result
                            baseLayerConfig.baseLayerList = sourceArr
                            this.$message.success('')
                        }
                    }
                )
            }).catch(() => {
                // 

            });
        },
        saveMapSource() {
            console.log('validate')
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    let check = checkUtil.isMapSource(this.form.url)
                    if (check) {
                        this.saveIsLoading = true
                        addMapSource(
                            {
                                name: this.form.name,
                                url: this.form.url
                            }
                        ).then(res => {
                            if (res.resultCode == 1) {
                                let sourceArr = res.result
                                baseLayerConfig.baseLayerList = sourceArr
                                this.$message.success('')
                                this.addDialogVisible = false
                            } else
                                this.$message.error('')
                            this.saveIsLoading = false
                        })
                    } else {
                        this.$message.error('')
                    }
                }
            })
        },
        closeDialog() {
            this.addDialogVisible = false;
        },
        changeBaseMap(index) {
            baseLayerConfig.setMapSource(index)
        }
    },
    computed: {
        nowBaseLayerName() {
            if (baseLayerConfig.getBaseLayerSource() == null) {
                return ''
            } else
                return baseLayerConfig.getBaseLayerSource().name
        }
    },
    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
            dialogVisible: false,
            addDialogVisible: false,
            baseLayerConfig,
            saveIsLoading: false,
            form: {
                name: '',
                url: ''
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                url: [
                    { required: true, message: 'URL', trigger: 'blur' }
                ]
            }
        }
    }
}
</script>
<style scoped>
.map-list-container {
    top: 100px;
    right: 16px;
}

.el-dialog {
    width: 600px;
}

.bord {
    font-weight: bold;
    color: red;
}
</style>


