import { service as request } from "./http" 

/**
 * 
 * @param {} data 
 * @returns 
 */
export function login(data) {
    return request({
        url: '/user/login',
        method: 'POST',
        data
    })
}

/**
 * 
 * @returns token
 */
export function getUserInfo() {
    return request({
        url: '/user/info',
        method: 'GET'
    })
}

export function register(data) {
    return request({
        url: '/user/register',
        method: 'POST',
        data
    })
}

/**
 * 
 * @param {} data 
 * @returns 
 */
export function changePassword(data) {
    return request({
        url: '/user/changePassword',
        method: 'POST',
        data
    })
}

export function getMySource() {
    return request({
        url: '/user/source/all',
        method: 'GET'
    })
}

/**
 * 
 * @param {} data 
 * @returns 
 */
export function addMapSource(data) {
    return request({
        url: '/user/source/add',
        method: 'POST',
        data
    })
}


/**
 * 
 * @param {} data 
 * @returns 
 */
export function delMapSource(sourceId) {
    return request({
        url: '/user/source/del?sourceId=' + sourceId,
        method: 'GET'
    })
}

/**
 * 
 * @param {} code 
 * @returns 
 */
export function active(code) {
    return request({
        url: '/user/active?activeCode=' + code,
        method: 'GET'
    })
}


export function splice(data) {
    return request({
        url: '/user/splice',
        method: 'POST',
        data
    })
}

/**
 * 
 * @param {*} data 
 * @returns 
 */
export function ps(data) {
    return request({
        url: '/user/ps',
        method: 'POST',
        data
    })
}

/**
 * 
 * @param {uuid} data 
 * @returns 
 */
export function getBoundingBoxInfo(uuid){
    return request({
        url: '/user/bounding?id='+uuid,
        method: 'GET'
    })
}



