<template>
  <el-dialog title="" :visible.sync="showDialog" width="800px" class="mydialog" :append-to-body="true"
    @close="handleClose">
    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <el-input clearable style="width: 300px;" v-model="info.name"></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20" style="font-weight: bold;">
        {{ info.mapSourceName }}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <span style="display: block;">
          {{ leftTopStr }}
          &emsp;{{ rightBottomStr }}
        </span>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <span v-html="info.info"></span>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <el-input-number v-model="info.zoom" :min="1" :max="20" label="" style="width: 150px;"></el-input-number>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
         <b>{{ totalTileSize }}</b>  <b>{{ picResolution }} </b>
      </el-col>
    </el-row>

    <!-- <el-row>
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <el-select v-model="info.grid" placeholder="" style="width: 150px;" @change="gridChange()">
          <el-option v-for="(item, index) in ['', '', '']" :key="index" :label="item" :value="index">
          </el-option>
        </el-select>
        <span><span style="color: red;">*</span>&emsp;CGCS200013</span>
      </el-col>
    </el-row>
    <el-row v-show="info.grid != 0">
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <el-color-picker v-model="info.gridColor" size="mini" style="vertical-align: middle;">
        </el-color-picker>
      </el-col>
    </el-row>

   <el-row v-show="info.grid != 0">
      <el-col :span="4"></el-col>
      <el-col :span="20">
        <el-input-number v-model="info.gridWidth" :min="1" :max="10" label="" size="mini"></el-input-number>
      </el-col>
    </el-row>  -->


    <span slot="footer" class="dialog-footer">
      <el-button @click="showDialog = false"></el-button>
      <el-button type="primary" @click="splice"></el-button>
    </span>
  </el-dialog>

</template>

<script>
import geoUtil from '@/util/geoUtil.js'
import { splice } from '../../util/request'
import { InfiniteScroll } from 'element-ui';

export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
      default: {
        leftTop: null, rightBottom: null,
        grid: 0,
        gridColor: '#000000',
        zoom: 15,
        gridWidth:2
      }
    }
  },
  data() {
    return {
      showDialog: false,
    };
  },
  watch: {
    show: function (n, o) {
      this.showDialog = n
    }
  },
  computed: {
    picResolution() {
      if (this.info.leftTop == null || this.info.rightBottom == 0) {
        return 0
      }
      return geoUtil.getPicResolution(this.info.leftTop, this.info.rightBottom, this.info.zoom)
    },
    totalTileSize() {
      if (this.info.leftTop == null || this.info.rightBottom == 0) {
        return 0
      }
      return geoUtil.calcTotalTileSize(this.info.leftTop, this.info.rightBottom, this.info.zoom)
    },
    leftTopStr() {
      return geoUtil.formatLatlng(this.info.leftTop)
    },
    rightBottomStr() {
      return geoUtil.formatLatlng(this.info.rightBottom)
    }
  },
  methods: {
    handleClose() {
      // 
      this.$emit('update:show', false)
    },
    splice() {
      if (this.totalTileSize > 1000) {
        this.$message.error('1000')
        return;
      }
      if (!this.$store.getters.vipIsAvailable) {
        if (this.totalTileSize > 100) {
          this.$message.error('vip100')
          return
        } else
          this.$confirm('vip100', '', { type: 'success', title: '' }).then(() => {
            //
            splice(this.info).then(res => {
              if (res.resultCode == 1) {
                let taskId = res.result
                this.$message.success(` ${taskId} ...`)
                this.$store.commit('addTask', taskId)
                this.showDialog = false
              } else
                this.$message.error(res.result)
            })
          })
      } else {
        //
        splice(this.info).then(res => {
          if (res.resultCode == 1) {
                let taskId = res.result
                this.$message.success(` ${taskId} ...`)
                this.$store.commit('addTask', taskId)
                this.showDialog = false
              } else
                this.$message.error(res.result)
        })
      }
    },
    gridChange() {
      console.log(this.info);
      if (this.info.grid == 2 && this.info.level < 13) {
        this.$message.error("13");
      }
    }
  }
};
</script>

<style scoped>
.dialog-footer {
  text-align: center;
}

.el-col {
  height: 50px;
  line-height: 50px;
}

.title {
  text-align: right;
  padding-right: 10px;
  font-weight: bold;
}
</style>


