<template>
  <div class="image-editor">
    <!--  -->
    <h1 class="editor-header">
      
    </h1>

    <!--  -->
    <div class="toolbar">
      <span></span> {{ `zoom${this.zoom} coord${this.maxX}-${this.maxY}-${this.minX}-${this.minY}` }}
      <span>:</span>
      <el-select v-model="gridType" placeholder="" style="width: 150px;">
        <el-option label="" value="none"></el-option>
        <el-option label="-" value="gaussian"></el-option>
        <!-- <el-option label="" value="latitudeLongitude"></el-option> -->
      </el-select>
      <span v-if="gridType !== 'none'">:</span>
      <el-color-picker v-if="gridType !== 'none'" v-model="gridColor" show-alpha
        style="margin-left: 10px;"></el-color-picker>
      <span v-if="gridType !== 'none'">:</span>
      <el-input v-if="gridType !== 'none'" v-model.number="gridLineWidth" type="number" placeholder=""
        style="width: 100px; margin-left: 10px;" :min="1"></el-input>
      <span v-if="gridType !== 'none'">:</span>
      <el-switch v-if="gridType !== 'none'" v-model="addTextEnabled" active-text="" inactive-text=""
        style="margin-left: 10px;"></el-switch>
      <el-button @click="saveImage" style="margin-left: 10px;" icon="el-icon-download"></el-button>
    </div>

    <!--  -->
    <div class="image-container" ref="imageContainer">
      <canvas ref="canvas"></canvas>
      <!-- <div class="zoom-controls">
        <el-button type="primary" icon="el-icon-zoom-in" @click="zoomIn"></el-button>
        <el-button type="primary" icon="el-icon-zoom-out" @click="zoomOut"></el-button>
      </div> -->
    </div>
  </div>
</template>

<script>
import { baseURL } from '@/util/http'
import { drawGaussGrid } from '@/util/grid'
import formatter from '@/util/geoUtil'

export default {
  data() {
    return {
      imagePadding: 30,
      imageFileName: this.$route.query.imageFileName,
      maxX: parseInt(this.$route.query.maxX) || 0,
      maxY: parseInt(this.$route.query.maxY) || 0,
      minX: parseInt(this.$route.query.minX) || 0,
      minY: parseInt(this.$route.query.minY) || 0,
      zoom: parseInt(this.$route.query.zoom) || 1,
      gridType: 'none', // nonegaussianlatitudeLongitude
      gridColor: '#000000', // 
      gridLineWidth: 1, // 
      canvas: null, // canvas 
      ctx: null, // canvas 
      img: null, // 
      scale: 1, // 
      loading: false, // 
      addTextEnabled: true // 

    };
  },
  computed: {
    imageUrl() {
      return `${baseURL}/user/down?name=${this.imageFileName}`
    }
  },
  methods: {
    //  canvas 
    initCanvas() {
      this.canvas = this.$refs.canvas;
      this.ctx = this.canvas.getContext('2d');

      // loading
      const loadingInstance = this.$loading({
        lock: true,
        text: ',...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      // 
      this.img = new Image();
      this.img.crossOrigin = 'anonymous'; // 
      this.img.src = this.imageUrl;

      // 
      this.img.onload = () => {
        loadingInstance.close();
        this.drawImage();
      };

      // 
      this.img.onerror = () => {
        loadingInstance.close();
        this.$message.error('');
      };
    },
    // 
    drawImage() {
      if (!this.ctx || !this.img) return;
      //  canvas 
      this.canvas.width = this.img.width + this.imagePadding * 2
      this.canvas.height = this.img.height + this.imagePadding * 2
      // 
      this.ctx.fillStyle = 'white'
      this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)
      // 20px
      this.ctx.drawImage(this.img, this.imagePadding, this.imagePadding, this.img.width, this.img.height);
      // 
      this.drawGrid();
    },
    // 
    drawGrid() {
      if (this.gridType === 'none') return;

      this.ctx.strokeStyle = this.gridColor;
      this.ctx.lineWidth = this.gridLineWidth;

      if (this.gridType === 'gaussian') {
        this.drawGaussianGrid();
      } else if (this.gridType === 'latitudeLongitude') {
        this.drawLatitudeLongitudeGrid();
      }
    },
    // 
    drawGaussianGrid() {
      drawGaussGrid(this.ctx,
        this.img.width,
        this.img.height,
        this.imagePadding,
        this.minX,
        this.minY,
        this.maxX,
        this.maxY,
        this.zoom,
        this.addTextEnabled)
    },
    // 
    drawLatitudeLongitudeGrid() {
      const latStep = this.canvas.height / 10; // 
      const lonStep = this.canvas.width / 10; // 

      // 
      for (let y = 0; y <= this.canvas.height; y += latStep) {
        this.ctx.beginPath();
        this.ctx.moveTo(0, y);
        this.ctx.lineTo(this.canvas.width, y);
        this.ctx.stroke();
      }

      // 
      for (let x = 0; x <= this.canvas.width; x += lonStep) {
        this.ctx.beginPath();
        this.ctx.moveTo(x, 0);
        this.ctx.lineTo(x, this.canvas.height);
        this.ctx.stroke();
      }
    },
    // 
    saveImage() {
      if (!this.canvas) return;
      // loading
      const loadingInstance = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      const quality = 0.5
      //  canvas 
      this.canvas.toBlob((blob) => {
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob); // Blob URL
        link.download = "canvas.png"; // 
        link.click(); // 
        loadingInstance.close()
      }, "image/png", quality);
    },
    // 
    zoomIn() {
      this.zoomf(1.1);
    },
    // 
    zoomOut() {
      this.zoomf(0.9);
    },
    // 
    zoomf(delta) {
      this.scale *= delta;
      //  0.5-3
      this.scale = Math.min(Math.max(0.5, this.scale), 3);
      // 
      this.ctx.setTransform(1, 0, 0, 1, 0, 0); // 
      this.ctx.scale(this.scale, this.scale);
      this.drawImage();
    }
  },
  watch: {
    // 
    gridType() {
      this.drawImage()
    },
    gridColor() {
      this.drawImage()
    },
    gridLineWidth() {
      this.drawImage()
    },
    addTextEnabled() {
      this.drawImage()
    }
  },
  mounted() {
    this.initCanvas()
  }
};
</script>

<style scoped>
.image-editor {
  display: flex;
  flex-direction: column;
  height: 100vh;
  /*  */
  padding: 10px;
  box-sizing: border-box;
}

.toolbar {
  flex-shrink: 0;
  /*  */
  margin: 0 0 20px 20px;
  /*     */
  display: flex;
  align-items: center;
  /*  */
  gap: 10px;
  /*  */
  font-size: 14px;
}

.image-container {
  flex-grow: 1;
  /*  */
  overflow: auto;
  /*  */
  border: 1px solid #ccc;
  padding: 10px;
}

canvas {
  display: block;
  max-width: 100%;
  height: auto;
}
</style>



