<template>
    <div class="header-container">
        <el-row style="height: 100%;">
            <el-col :span="12" class="header-left">
                <img style="width: 60px;height: 60px;" :src="logo" />
                <span class="system-name"></span>
                <span @click="changeLatlngFormat" class="center-coord" v-html="cc"
                    style="line-height: 60px;height: 60px;"></span>
            </el-col>
            <el-col :span="12" class="header-right">
                <span class="menu" @click="showWXQRDailog = true"></span>
                <span class="menu" @click="showInstruction = !showInstruction"></span>
                <span class="menu" style="color: red;" @click="openLink()"></span>
                <el-dropdown trigger="click">
                    <div class="user-manager">
                        <div class="user-name" v-if="getUser">{{ getUser.userName }}&emsp;<el-tag>{{ getUserType
                                }}</el-tag></div>
                        <div class="user-name" v-else></div>
                    </div>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item
                            @click.native="showUserInfoDialog = !showUserInfoDialog"></el-dropdown-item>
                        <el-dropdown-item
                            @click.native="showChangePasswordDailog = !showChangePasswordDailog"></el-dropdown-item>

                        <el-dropdown-item @click.native="startActive()"></el-dropdown-item>
                        <el-dropdown-item divided @click.native="logout"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-row>

        <el-dialog :visible.sync="showUserInfoDialog" title="" width="600px" class="mydialog">
            <el-row>
                <el-col :span="8">
                    <div class="title">:</div>
                </el-col>
                <el-col :span="16">
                    <span>{{ getUser.userName }}</span>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="8">
                    <div class="title">:</div>
                </el-col>
                <el-col :span="16">
                    <span>{{ formatTimestamp(getUser.createTime) }}</span>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <div class="title">:</div>
                </el-col>
                <el-col :span="16">
                    <span>{{ formatTimestamp(getUser.lastIn) }}</span>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <div class="title">:</div>
                </el-col>
                <el-col :span="16">
                    <span v-html="getUserType"></span>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <div class="title">:</div>
                </el-col>
                <el-col :span="16">
                    <span>{{ getUser.expiration == 0 ? '-' : formatTimestamp(getUser.expiration) }}</span>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showUserInfoDialog = false"></el-button>
            </span>
        </el-dialog>

        <el-dialog :visible.sync="showActiveDialog" title="" width="600px" class="mydialog">
            <el-input v-model="activationCode" placeholder=""></el-input>
            <div style="text-align: left;  margin-top: 10px;  font-size: 12px;">
                <span v-if="activationCode === ''" style="color: red;">*</span>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="saveActivationCode" :loading="activeLoading"></el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="showWXQRDailog" width="400px" class="mydialog">
            <div style="text-align: center;">
                <img :src="wechat" style="width: 120px;height: 120px;" />
            </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="showInstruction" width="600px" class="mydialog">
            <div style="text-align: left;max-height: 700px;overflow-y: auto;">
                <h2></h2>
                <ul>
                    <li>
                        <h3>step1 </h3>
                        <p>
                            <img :src="picStep1" class="instruction-img">
                        </p>
                    </li>
                    <li>
                        <h3>step2 1000</h3>
                        <p>
                            <img :src="picStep2" class="instruction-img">
                        </p>
                    </li>
                    <li>
                        <h3>step3 </h3>
                        <p>
                            <img :src="picStep3" class="instruction-img">
                        </p>
                    </li>
                    <li>
                        <h3>step4 </h3>
                        <p>
                            <img :src="picStep4" class="instruction-img">
                        </p>
                    </li>
                </ul>
                <h2></h2>
                <p>1.webp</p>
                <p>2.2</p>
                <p>3.10001000</p>
                <p>4.vip100</p>
                <p>5.</p>
                <h2></h2>
                <p> 1.30</p>
                <p> 2.99</p>
            </div>
        </el-dialog>
        <change-password-dialog :show.sync="showChangePasswordDailog"></change-password-dialog>
    </div>
</template>

<script>
import formatter from '@/util/geoUtil';
import util from '@/util/commonUtil.js'
import ChangePasswordDialog from './ChangePassword.vue'
import { active } from '@/util/request';

export default {
    components: {
        'change-password-dialog': ChangePasswordDialog
    },
    name: 'Header',
    props: ["cc"],
    data() {
        return {
            showWXQRDailog: false,
            showActiveDialog: false,
            showUserInfoDialog: false,
            showSuccessDialog: false,
            activationCode: '',
            showInstruction: false,
            activeLoading: false,
            picStep1: require('@/assets/step1.png'),
            picStep2: require('@/assets/step2.png'),
            picStep3: require('@/assets/step3.png'),
            picStep4: require('@/assets/step4.png'),
            wechat: require('@/assets/wechat.jpeg'),
            logo: require('@/assets/txz-logo.png')
        }
    },
    computed: {
        getUser() {
            return this.$store.state.user
        },
        getUserType() {
            if (!this.getUser) {
                return ''
            }
            switch (this.getUser.type) {
                case 1:
                    return `vip`
                case 2:
                    return `vip`
                case 0:
                default:
                    return ``
            }
        }
    },
    methods: {
        openLink() {
            this.$confirm('', '', {
                confirmButtonText: '',
                type: 'info'
            })
                .then(() => {
                    // 
                    window.open('http://hxy2020.com', '_blank');
                })

        },
        startActive() {

            if (this.$store.getters.vipIsAvailable) {
                this.$message.info('vip')
                return
            }
            this.showActiveDialog = !this.showActiveDialog
        },
        formatTimestamp(timestamp) {
            return util.formatTimestamp(timestamp)
        },
        logout() {
            this.$store.commit('clearUser')
            this.$router.push('/login')
        },
        saveActivationCode() {

            if (this.activationCode.trim() == '') {
                return
            }
            this.activeLoading = true
            active(this.activationCode).then(res => {
                this.activeLoading = false
                if (res.resultCode == -1) {
                    this.$message.error(res.info)
                }
                if (res.resultCode == 1) {
                    this.showActiveDialog = false
                    this.$confirm(',', '', {
                        confirmButtonText: '',
                        type: 'success',
                    })
                        .then(() => {
                            // 
                            this.logout()
                        })
                        .catch(() => {
                            // 

                        });

                }
            })
        },
        changeLatlngFormat: function () {
            formatter.changeLatlngFormat()
            this.$message.success('')
        }
    },
    mounted: function () {

    }
}
</script>
<style scoped>
.instruction-img {
    max-width: 400px;
    display: block;
    margin: 0 auto;
    padding: 10px 0;
}

.header-container {
    background-color: white;
    width: 100%;
    padding: 0 20px;
    border: 1px solid white;
    border-top: 0;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .25);
}

.header-right {
    text-align: right;
    height: 60px;
    line-height: 60px;
}

.header-left {
    text-align: left;
    height: 60px;
    line-height: 60px;
    display: flex;
    align-items: center;
}

.system-name {
    padding: 0px 20px;
    font-size: 1.6em;
    color: black;
}

.title {
    text-align: right;
    color: black;

}

.menu {
    cursor: pointer;
    padding: 0px 10px;
}

.center-coord {
    margin-left: 10px;
    line-height: 60px;
    color: black;
    font-size: 16px;
    cursor: pointer;
}

.user-manager {
    padding: 0px 10px;
    margin-left: 10px;
    cursor: pointer;
}

.el-dropdown {
    height: 100%;
}

.user-name {
    height: 30px;
    line-height: 30px;
}
</style>



